import React, { useState } from 'react'
import axios from 'axios'

import Btn from '../Btn/Btn.jsx'
import Label from '../Label/Label.jsx'
import StyledStrong from '../StyledStrong/StyledStrong.jsx'

import {
    StyledNewsletterForm,
    StyledNewsletterWrapper,
    StyledInput,
    StyledRadioGroup,
    StyledFormResponse,
    StyledCheckbox
} from './NewsletterFormStyles'

const FormResponse = ({ response }) => {
    return <StyledFormResponse resType={response.success}>{response.message}</StyledFormResponse>
}

const NewsletterForm = ({ layout }) => {
    const [isLoading, setIsLoading] = useState(false)
    const [isSent, setIsSent] = useState(false)
    const [formResponse, setFormResponse] = useState('')

    const emailRef = React.createRef()
    const checkboxRef_1 = React.createRef()
    const checkboxRef_2 = React.createRef()

    const handleSubmission = async e => {
        e.preventDefault()
        setIsLoading(true)
        axios.post(`${process.env.NODE_ENV === 'development' ? 'http://localhost' : 'https://korkizfrontendu.pl'}/subscribe/newsletter`, {
            email: emailRef.current.value,
            terms_accepted: checkboxRef_1.current.checked,
            user_interested: checkboxRef_2.current.checked
        })
            .then(res => {
                setTimeout(() => {
                    setIsLoading(false)
                    setIsSent(true)
                    setFormResponse({
                        success: true,
                        message: res.data.message,
                        err: res.data.err
                    })
                }, 1000)
            })
            .catch(err => {
                setIsLoading(false)
                setIsSent(false)
                setFormResponse({
                    success: false,
                    message: '',
                    err: err
                })
            })
    }

    return (
        <StyledNewsletterForm isLoading={isLoading} layout={layout} onSubmit={handleSubmission}>
            <StyledNewsletterWrapper mobileLayout="column" layout="column">
                <h3>
                    <StyledStrong>
                        Bdmy w kontakcie!
                    </StyledStrong>
                </h3>
                <p>
                    Jeeli formua <strong>Korkw z Front-endu</strong> wydaje Ci si interesujca, zapisz si do <strong>newslettera</strong>, a ju wkrtce dostaniesz informacje o planowanym starcie programu!
                </p>
            </StyledNewsletterWrapper>
            <StyledNewsletterWrapper mobileLayout="column" isSent={isSent}>
                <Label srOnly htmlFor="email">
                    Twj e-mail
                </Label>
                <StyledInput ref={emailRef} width="70%" type="email" name="email" placeholder="Wpisz e-mail" required />
                <Btn width="30%" type="submit">
                    Zapisuj si
                </Btn>
            </StyledNewsletterWrapper>
            {formResponse && <StyledNewsletterWrapper><FormResponse response={formResponse} /></StyledNewsletterWrapper>}
            <StyledNewsletterWrapper layout="column" isSent={isSent}>
                <StyledRadioGroup>
                    <StyledCheckbox
                        aria-label="Akceptuj regulamin"
                        aria-required="true"
                        ref={checkboxRef_1}
                        type="checkbox"
                        name="terms_accepted"
                        id="terms_accepted"
                        required
                    />
                    <Label htmlFor="terms_accepted">
                        <span>
                            * Zapisujc si do newslettera wyraasz zgod na otrzymywanie informacji o nowociach, produktach i usugach <strong>korkizfrontendu.pl</strong> i <strong>sors Micha Trykoszko</strong> oraz akceptujesz <a href="/regulamin">regulamin</a>. Pamitaj, e <strong>nigdy nie wyl Ci spamu</strong>
                        </span>
                    </Label>
                </StyledRadioGroup>
                <StyledRadioGroup>
                    <StyledCheckbox
                        aria-label="Jestem wstpnie zainteresowany"
                        aria-required="false"
                        ref={checkboxRef_2}
                        type="checkbox"
                        name="user_interested"
                        id="user_interested"
                    />
                    <Label htmlFor="user_interested">
                        <span>
                            <strong>Jestem wstpnie zainteresowany</strong> szkoleniem i chciabym uzyska o nim wicej informacji (zaznaczenie tego punktu nie oznacza rejestracji w programie).
                        </span>
                    </Label>
                </StyledRadioGroup>
            </StyledNewsletterWrapper>
        </StyledNewsletterForm>
    )
}

export default NewsletterForm
